import { FC, ReactChild } from 'react';
import classNames from 'classnames';
import './blur.css';

interface Props {
	children: ReactChild;
	className?: string;
}

const BackgroundBlur: FC<Props> = ({ children: child, className }) => {
	return (
		<div className={classNames('wrapper', className)}>
			<div className="wrapper__blur-background" />
			{child}
		</div>
	);
};

export default BackgroundBlur;
