import { FC } from 'react';
import { ReactComponent as Logo } from '../../assets/logo.svg';
import BackgroundBlur from '../backround-blur/blur';
import './header.css';

const Header: FC = () => {
	return (
		<header className="header white-overlay">
			<BackgroundBlur className="header__wrapper">
				<nav className="wrapper__nav white-overlay white-overlay--dense">
					<Logo className="nav__logo" width="100px" />
				</nav>
			</BackgroundBlur>
		</header>
	);
};

export default Header;
